#QWHeader {
  background: -webkit-linear-gradient(top, #4B78CD 0%, #65C2E7 100%);
  background: linear-gradient(to bottom, #4B78CD 0%, #65C2E7 100%);
  padding: 100px 0px;
  text-align: center;
  color: white; }
  #QWHeader h1 {
    font-size: 30px;
    font-weight: 400;
    margin-bottom: 20px; }
  #QWHeader h2 {
    font-size: 22px;
    font-weight: 400;
    opacity: 0.7;
    margin-bottom: 20px; }
  #QWHeader a {
    display: inline-block;
    box-sizing: border-box;
    height: 40px;
    line-height: 40px;
    padding: 0px 20px;
    border: 1px solid white;
    border-radius: 5px;
    color: white;
    text-decoration: none; }
    #QWHeader a:hover {
      background-color: rgba(255, 255, 255, 0.1); }
    #QWHeader a:active {
      background-color: rgba(255, 255, 255, 0.5); }

